.scheduleFlights {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  background-color: rgb(246, 246, 246);
  margin: 40px 80px;
  padding: 30px 50px;
}
.departureAndReservation {
  width: 68%;
  display: flex;
  flex-direction: column;
}

.reservation__selectSeats {
  height: fit-content;
  padding: 10px 40px;
  border: 2px solid rgb(158, 36, 123);
  background-color: rgb(158, 36, 123);
  border-radius: 20px;
  font-size: 13px;
  color: white;
  font-weight: 700;
  align-self: center;
  cursor: pointer;
  &:hover {
    background-color: white;
    color: rgb(158, 36, 123);
    transition: all 0.3s ease;
    transform: scale(1.1);
  }
}

.displayNone {
  display: none;
}

.containerReservation {
  display: flex;
  flex-direction: column;
}
